.isoSignInPage {
    width: 100%;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background: url("../../image/theme.png") 100% 100% no-repeat;
    background-size: cover;
    overflow: hidden;
    font-family: serif;
    /*min-width: 550px;*/
    /*text-align: center;*/
    padding-bottom: 100px;
}

